.QuizCardContainer {
    position: relative;
    display: flex;
    border: 1px solid var(--neutral200Color);
    color: var(--neutral900Color);
    cursor: pointer;
    margin: 16px 0;
    padding: 0 16px 0 0 ;
    background: var(--answerOptionColor);
    border-radius: var(--answerOptionRadius);
}

html[dir="rtl"] .QuizCardContainer {
    padding: 0 0 0 16px;
}

.QuizCard {
    display: flex;
    width: 100%;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
}

.QuizCardContent {
    flex-grow: 1;
    padding: 28px 0 28px 32px;
}

html[dir="rtl"] .QuizCardContent {
    flex-grow: 1;
    padding: 28px 32px 28px 0;
}

.QuizCardHeader {
    color: var(--neutral900Color);
    font-size: 16px;
    font-weight: 600;
    line-height: 24px;
}

.QuizCardDescription {
    color: var(--neutral600Color);
    font-size: 14px;
    line-height: 20px;
}

.RedCheckmark {
    width: 20px;
}

.Answer {
    border: 1px solid var(--primary400Color);
}

@media (max-width: 500px) {
    .QuizCardContainer {
        width: 60%;
        min-height: 72px;
    }
    .QuizCardContent {
        padding: 16px 16px 16px 24px;
    }
}